body {
  max-width: 100%;
  overflow-x: hidden;
}

.App {
  display: flex;
  direction: rtl;
  width: 100%;
  padding: 25px;
}

.view {
  width: 75%;
  direction: ltr;
}

.sidebar {
  width: 25%;
  direction: ltr;
}

@media (orientation: landscape) {
  .App {
    flex-direction: row;
  }
}

@media (orientation: portrait) {
  .App {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
  }

  .view {
    width: 100%;
  }
}
